/*

<input type="text" autocapitalize="off" />
<input type="text" autocorrect="off" />

 */
/*  */
html {
    // -webkit-text-size-adjust: 100%;
}
/* , ios,android,winphone */
body{
    font-family:Helvetica;
}
/* 1.android a/button/input 2.ios a */
a,button,input,textarea {
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    -webkit-appearance: none; /* 2 */
    // -webkit-user-modify
    // -webkit-user-modify:read-write-plaintext-only;
}
/*  android  */
input::-webkit-input-speech-button {display: none}
/*   */
.css {
   -webkit-transform: translate3d(0, 0, 0);
   -moz-transform: translate3d(0, 0, 0);
   -ms-transform: translate3d(0, 0, 0);
   transform: translate3d(0, 0, 0);
}
/*  transition */
.css{
    /* 3D  3D*/
    -webkit-transform-style: preserve-3d;
    /**/
    -webkit-backface-visibility: hidden;
}
//
@media all and (orientation:portrait) {
    .css{}
}

//
@media all and (orientation:landscape) {
    .css{}
}





.ta-left {
    text-align: left;
}
.ta-center {
    text-align: center;
}
.ta-right {
    text-align: right;
}

#main {
    padding-top: $bodyTop;
    position: absolute;
    z-index: 9;

}

.breadcrumb {
    width: 100%;
    text-align: left;
    padding: 1em 1em 0 1em !important;
}


// index.html
#topic_list {
    background: #FFF;
    cursor: pointer;
}

.index-page {


    .index-header {
        padding: 0.5em 1em;
    }

    .inner {
        max-width: none;
        padding: 0.5em 1em !important;
        width: 100% !important;
    }

    .last_time {
        margin-left: 0 !important;
        padding: 0.5em !important;
    }

    .topic-tab {
        margin: 0 1em 0 0;
    }

    .no-topic {
        height: 100px;
        line-height: 100px;
        // vertical-align: middle;
    }

}


// message.html
.message-page {

    margin-right: 0 !important;

    .no-topic {
        height: 100px;
        line-height: 72px;
        margin: 1em;
        overflow: hidden;

        p {
            background: #FFF;
            margin-bottom: 1em;
        }

    }


}

// about.html
.about-page {

    margin-right: 0 !important;

    .topic {
        margin: 1em;
        overflow: hidden;
    }


}

// guide.html
.guide-page {

    margin-right: 0 !important;

    .topic {
        margin: 1em;
        overflow: hidden;
    }

}

// setting.html
.setting-page {

    margin-right: 0 !important;

    .segment {
        margin: 1em;
        overflow: hidden;
    }

}

// signin.html
// signup.html
.signin-page,
.signup-page {

    margin-right: 0 !important;
    a:hover {
        text-decoration: none;
    }

    .segment {
        margin: 1em;
        overflow: hidden;
    }

}


// common
#backtotop {
    width: 3em;
    color: #000;
    padding: 12px 0px 12px 5px;
    display: none;
    position: fixed;
    cursor: pointer;
    text-align: center;
    z-index: 20;
    background-color: rgba(91, 222, 210, 0.77);
    border-radius: 12px 0px 0px 12px;
    // margin-right: 1em;
}






